/*  ====== Basic Styles ======
    Global content styles for all basic elements
    ========================================================================= */
body {
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
}
/*  --- Body Text ---
    ------------------------------------------------------------------------- */
p {
  margin: 0;
  padding: 0;
  line-height: 1.6;
}
p.lead {
  font-size: 30px;
  line-height: 1.4;
  font-weight: 300;
}
@media only screen and (max-width: 992px) {
  p.lead {
    font-size: 24px;
  }
}
@media only screen and (max-width: 768px) {
  p.lead {
    font-size: 20px;
  }
}
a {
  font-weight: bold;
  color: #ed913d;
  -webkit-transition: -webkit-transform 0.2s ease-in-out, color 0.2s ease-in-out, background 0.2s ease-in-out, border 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, color 0.2s ease-in-out, background 0.2s ease-in-out, border 0.2s ease-in-out;
}
a:hover {
  color: #b35e11;
  text-decoration: none;
}
/*  --- Headings ---
    ------------------------------------------------------------------------- */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 10px 0;
  padding: 0;
  font-family: "Roboto Slab", Georgia, "Times New Roman", Times, serif;
  color: rgba(0, 0, 0, 0.4);
  line-height: 1.4;
}
h1 small,
h2 small,
h3 small,
h4 small,
h5 small,
h6 small {
  display: inline-block;
}
h1 small:before,
h2 small:before,
h3 small:before,
h4 small:before,
h5 small:before,
h6 small:before {
  content: ' ';
}
h1.pageHeading,
h2.pageHeading,
h3.pageHeading,
h4.pageHeading,
h5.pageHeading,
h6.pageHeading {
  text-align: center;
  font-size: 64px;
  line-height: 1.2;
  color: rgba(0, 0, 0, 0.4);
  text-shadow: #ffffff 1px 1px 1px, rgba(0, 0, 0, 0.2) -1px -1px 1px;
}
@media only screen and (max-width: 768px) {
