/*  --- Lists ---
    ------------------------------------------------------------------------- */
ul {
  margin: 0;
  padding: 0;
}
ul li {
  margin-bottom: 10px;
  margin-left: 30px;
}
dl dt {
  font-weight: bold;
}
dl dd {
  margin-bottom: 10px;
}
/*  --- Blockquote ---
    ------------------------------------------------------------------------- */
blockquote {
  display: inline-block;
  font-family: Georgia, cursive;
  font-style: italic;
  font-size: 32px;
  line-height: 40px;
  color: #636363;
  background-image: url('../img/blockquote_open.svg'), url('../img/blockquote_close.svg');
  background-position: left top, right bottom;
  background-repeat: no-repeat;
  margin: 0;
  padding: 15px 30px;
}
blockquote p {
  margin-bottom: 0 !important;
}
blockquote cite {
  font-style: italic;
  font-size: 24px;
}
blockquote cite::before {
  content: '-- ';
}
blockquote.pull-quote {
  margin: 30px;
  width: 50%;
  float: right;
}
@media screen and (max-width: 768px) {
  blockquote.pull-quote {
    width: 100%;
  }
}
/*  --- Horizontal Rule ---
    ------------------------------------------------------------------------- */
hr {
  border-top: rgba(0, 0, 0, 0.2) 2px dotted;
}
hr.spacer {
  padding: 0;
  margin: 0;
  height: 30px;
  width: 30px;
  visibility: hidden;
  /*
        &:last-child,
        &:first-child {
            display: none;
        }
        */
}
hr.spacer.-sm {
  height: 15px;
}
hr.spacer.-lg {
  height: 60px;
}
hr.spacer.-xl {
  height: 90px;
}
/*  --- Media ---
    ------------------------------------------------------------------------- */
img {
  border: none;
  max-width: 100%;
  height: auto;
}
img.-center {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
/*  --- Code & Pre ---
    ------------------------------------------------------------------------- */
code {
  display: inline-block;
  color: rgba(255, 255, 255, 0.8);
  background: rgba(0, 0, 0, 0.7);
  padding: 0px 10px;
  border-radius: 3px;
  white-space: pre-wrap;
  text-align: left;
}
pre code {
  padding: 10px;
  padding: 30px 40px;
  margin: 10px;
  -webkit-box-shadow: inset rgba(255, 255, 255, 0.6) -1px -1px 1px, inset rgba(0, 0, 0, 0.4) 0 0 6px;
  box-shadow: inset rgba(255, 255, 255, 0.6) -1px -1px 1px, inset rgba(0, 0, 0, 0.4) 0 0 6px;
  border-radius: 10px;
}
/*  --- Table ---
    ------------------------------------------------------------------------- */
table {
  width: 100%;
}
table th {
  background: rgba(0, 0, 0, 0.2);
  text-align: left;
}
table th,
table td {
  padding: 10px;
  border: 1px solid rgba(0, 0, 0, 0.2);
}
