/*  ====== Content ======
    Apply 'Content' class to blocks of content.
    Useful for user-provided markup (TinyMCE etc.)
    ========================================================================= */
.Content > *:last-child {
  margin-bottom: 0;
}
.Content p {
  margin-bottom: 1em;
  word-wrap: break-word;
  -webkit-hyphens: auto;
  -moz-hyphens: auto;
  -ms-hyphens: auto;
  -o-hyphens: auto;
  hyphens: auto;
}
.Content a {
  display: inline-block;
  font-weight: bold;
  position: relative;
  color: #e27715;
  border-bottom: 1px solid transparent;
}
.Content a:hover {
  -webkit-transform: scale(0.9);
  -ms-transform: scale(0.9);
  -o-transform: scale(0.9);
  transform: scale(0.9);
  color: #4D78C9;
  border-bottom: 1px solid rgba(0, 0, 0, 0.7);
}
.Content a:hover img {
  opacity: 0.6;
}
.Content a:active {
  -webkit-transform: scale(1.4);
  -ms-transform: scale(1.4);
  -o-transform: scale(1.4);
  transform: scale(1.4);
}
.Content a img {
  -webkit-transition: opacity 0.2s ease-in-out;
  -o-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
}
.Content h1,
.Content h2,
.Content h3,
.Content h4,
.Content h5 {
  margin-bottom: 20px;
}
.Content ul li,
.Content ol li {
  margin-bottom: 10px;
}
.Content ul.list-spaced li,
.Content ol.list-spaced li {
  margin-bottom: 27px;
}
.Content blockquote {
  margin-bottom: 30px;
}
